@import "_variables";
@import "fonts";
@import "reset";
@import url('https://fonts.googleapis.com/css2?family=B612+Mono:ital,wght@0,400;0,700;1,400&family=DM+Serif+Display&display=swap');

@font-face {
  font-family: Ogg-Thin;
  src: url("../../fonts/Ogg-Thin-BF646c18fc4eb07.otf") format("opentype");
}

@font-face {
  font-family: Ogg-Light;
  src: url("../../fonts/Ogg-Light-BF646c18fbb73f7.otf") format("opentype");
}

@font-face {
  font-family: Ogg-Regular;
  src: url("../../fonts/Ogg-Regular-BF646c18fc3ed57.otf") format("opentype");
}

@font-face {
  font-family: OggText-Light;
  src: url("../../fonts/OggText-Light-BF646c18fa863ce.otf") format("opentype");
}

@font-face {
  font-family: OggText-Medium;
  src: url("../../fonts/OggText-Medium-BF646c18fbe3cb2.otf") format("opentype");
}


html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  width: 100%;
  background-size: cover;
}

body {
  line-height: 1.2;
  background-color: white;
}

main {
  width: calc(100% - 180px);
}

.hero {
  padding: 250px 60px 120px;

  h1 {
    font-family: Ogg-Light;
    font-size: 4.8em;
    max-width: 1030px;
    width: 100%;
  }
}

.hero-img {
  margin-bottom: 60px;

 }

.market-section {
  padding: 60px;

  h1 {
    font-family: Ogg-Light;
    font-size: 8.6rem;
    max-width: 1030px;
    width: 100%;
    padding-bottom: 60px;
  }

  .market {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3em;

    .market-list {
      display: flex;
      flex-direction: column;
      gap: 2em;
      font-family: 'B612 Mono', monospace;
      font-size: 12px;
      line-height: 1.6em; 
    }
  }
}

.home-events {
  padding: 60px 60px 150px;
  
   h1 {
    font-family: Ogg-Light;
    font-size: 6.6rem;
    max-width: 1030px;
    width: 100%;
    padding-bottom: 60px;
  }

  .home-events-header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 60px;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .home-events__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    margin-top: 10px;


  .home-events__row-item {
    font-family: 'B612 Mono', monospace;
    text-align: center;
    justify-items: center;
    padding-top: 60px;

    span {
      margin: 0 auto 15px;
      font-size: 12px;
      text-align: center;
      display: block;
    }
     
    p {
      font-size: 12px;
      line-height: 1.6em;
      align-items: center;
      justify-content: center;
      text-align: center;
      margin: 0;
      padding: 0;
    }
 
  }
  
}

.home-events__row > a {
    padding: 20px;
    width: 180px;
    position: relative;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
    grid-column-start: span 3;
    background-color: green;
  }

}

.home-events__row {
  width: 100%; 
  background-image: linear-gradient(90deg,#000 50%,transparent 0),linear-gradient(90deg,#000 50%,transparent 0),linear-gradient(90deg,#000 50%,transparent 0),linear-gradient(90deg,#000 50%,transparent 0);
  background-size: 30px 100%; /* Adjust the size of dashes and gaps */
  transition: background-position 0.2s ease-in-out; /* Smooth transition */
  background-position: 0 0,0 100%,left 14px,0 calc(100% - 14px);
  background-repeat: repeat-x,repeat-x;
  background-size: 24px 1px,24px 1px,24px 1px,24px 1px;
}


